#active-cases{

  .flex-container{
    display: flex;
    justify-content: space-evenly;
  }

  .join-button{
    border: 1px solid black;
    padding: .5em;
  }

  ul{
    padding-left: 0;
    font-size: .8em;
    width: 100%;
    li{
      border: 1px solid black;
      padding: 1em;
      overflow: auto;

      .more-dots{
        position: relative;
        top: -18px;
        right: 1px;
      }
    }
  }
}